/*headlines*/
%headers {
  font-size: 16px;
  color: $headline-color;
  font-family: $roboto-font;
}

h5 {
  @extend %headers;
  line-height: 42px;
  font-weight: 500;
  text-transform: uppercase;
  border-bottom: 1px solid $border-color;
  padding: 1px 16px;
}

h6 {
  @extend %headers;
  line-height: 42px;
  font-weight: 700;
  padding-top: 42px;
}

// paragraph
p {
  @extend %headers;
  font-weight: 700;
}
