// topbar
.topbar {
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  line-height: 41px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background-color: $white-color;
  border-bottom: 1px solid $border-color;
  z-index: 1;

  header {
    float: right;

    img {
      width: 18px;
    }
  }

  ul {
    li {
      display: block;
      float: left;

      a {
        float: right;
        text-align: center;
        padding: 6px 16px;
        text-decoration: none;
        border-left: 0.5px solid $border-color;
        font-size: 20px;
        line-height: 42px;
        color: $paragraph-color;
        font-family: $roboto-font;
        font-weight: 400;

        span {
          margin-right: 9px;
          font-size: 16px;
        }

        &:hover {
          background-color: $border-color;
          color: $white-color;
        }

        &:active {
          color: $white-color;
        }
      }

      .topbar-paragraph {
        font-size: 13px;
        line-height: 42px;
        color: $name-color;
        font-family: $roboto-font;
        font-weight: 300;
      }

      .topbar-wallet {
        font-size: 20px;
        line-height: 42px;
        color: $paragraph-color;
        font-family: $roboto-font;
        font-weight: 400;
      }
    }
  }
}
// modal

.modal {
  background: $white-color;
  display: flex;
  flex-direction: column;
  width: 200px;
  height: 100px;
  text-align: center;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 50%;
  margin-top: -50px;
  left: 50%;
  margin-left: -100px;
  line-height: 25px;
  z-index: 99;
}

.grow:hover {
  -webkit-transform: scale(1.3);
  -ms-transform: scale(1.3);
  transform: scale(1.3);
}

.modal a {
  line-height: 1em;
  text-align: center;
}

.modal-bg {
  background: $paragraph-color;
  opacity: 0.8;
  filter: alpha(opacity=80);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9;
}

@media (max-width: 767px) {
  /*small devices*/
  header {
    height: auto;
  }

  main.fullwidth .main,
  .main {
    margin-left: 90px;
  }

  .main-center {
    width: 100%;
  }

  .topbar {
    position: fixed;
  }
}

@media (max-width: 470px) {
  .topbar {
    .wallet,
    .notification {
      display: none;
    }
  }
}

@media not all and (hover: none) {
  .topbar a:hover {
    background-color: $topbar-hover-color;
    opacity: 0.5;
  }
}
